}

.slider {
  position: relative;
}

.slider__toggles {
  display: flex;
  justify-content: center;
  column-gap: 19px;
  align-items: center;
  margin: 0;
}
@media (min-width: 1200px) {
  .slider__toggles {
    display: none;
  }
}
.slider__toggles--desktop {
  display: none;
}
@media (min-width: 1200px) {
  .slider__toggles--desktop {
    width: 940px;
    top: 141px;
    position: absolute;
    display: flex;
    justify-content: space-between;
  }
  .slider__toggles--desktop .slider__toggle {
    width: auto;
    height: auto;
    background-color: transparent;
    border-radius: none;
    opacity: 0.3;
    cursor: pointer;
  }
  .slider__toggles--desktop .slider__toggle:hover {
    opacity: 1;
  }
  .slider__toggles--desktop .slider__toggle:active {
    opacity: 0.1;
  }
}

.slider__toggle {
  width: 6px;
  height: 6px;
  background-color: #283645;
  border-radius: 50%;
  padding: 0;
  border: none;
  opacity: 0.3;
  display: block;
}
.slider__toggle:hover {
  opacity: 0.6;
}
.slider__toggle:active {
  opacity: 0.1;
}
.slider__toggle--left::before {
  position: absolute;
  left: 0;
  top: 0;
  width: 21.706px;
  height: 41.719px;
  content: "";
  background-image: url("../img/icon-review-arrow-left.svg");
}
.slider__toggle--right::before {
  position: absolute;
  right: 0;
  top: 0;
  width: 21.706px;
  height: 41.719px;
  content: "";
  background-image: url("../img/icon-review-arrow-right.svg");
}

.slider__toggle--current {
  position: relative;
  opacity: 1;
  background-color: #ffffff;
}
.slider__toggle--current::before {
  top: -3px;
  left: -3px;
  content: "";
  width: 6px;
  height: 6px;
  position: absolute;
  border: 3px solid #283645;
  border-radius: 50%;
}

@media (max-width: 1199px) {
  .logo:hover .logo__text,
.logo:hover .logo__glasses {
    fill: #D22856;
  }
  .logo:active {
    opacity: 0.3;
  }
  .logo:active .logo__text,
.logo:active .logo__glasses {
    fill: #ffffff;
  }
}
.logo__textWithAsterisk {
  display: none;
}

.logo__glasses {
