}

.mainHeader--navClosed .mainHeader__logoLink {
  padding-top: 22px;
  padding-bottom: 21px;
}
.mainHeader--navClosed .mainHeader__nav {
  display: none;
}
@media (min-width: 1200px) {
  .mainHeader--navClosed .mainHeader__nav {
    display: block;
  }
}
.mainHeader--navClosed .mainHeader__header {
  background-color: rgba(0, 0, 0, 0.3);
  border: none;
}
@media (min-width: 1200px) {
  .mainHeader--navClosed .mainHeader__header {
    background-color: transparent;
  }
}
.mainHeader--navClosed .mainHeader__toggle {
  min-height: 66px;
}
@media (min-width: 660px) {
  .mainHeader--navClosed .mainHeader__toggle {
    padding: 43px 0;
  }
}
.mainHeader--navClosed .mainHeader__toggle::before {
  top: 21px;
  position: absolute;
  content: "";
  right: 0;
  width: 50px;
  height: 4px;
  border-radius: 1px;
  background-color: var(--white);
  box-shadow: 0 10px 0 0 #ffffff, 0 20px 0 0 #ffffff;
}
@media (min-width: 660px) {
  .mainHeader--navClosed .mainHeader__toggle::before {
    top: 41px;
  }
}
.mainHeader--navClosed .mainHeader__toggle:hover::before {
  background-color: #D22856;
  box-shadow: 0 10px 0 0 #D22856, 0 20px 0 0 #D22856;
}
.mainHeader--navClosed .mainHeader__toggle:active::before {
  background-color: rgba(210, 40, 86, 0.3);
  box-shadow: 0 10px 0 0 rgba(210, 40, 86, 0.3), 0 20px 0 0 rgba(210, 40, 86, 0.3);
}

@media (max-width: 1199px) {
  .mainHeader--navOpened {
    background-image: none;
  }
}
.mainHeader--navOpened .mainHeader__toggle::before, .mainHeader--navOpened .mainHeader__toggle::after {
  position: absolute;
  content: "";
  top: 33px;
  right: -3px;
  width: 30px;
  height: 3px;
  background-color: var(--white);
}
@media (min-width: 660px) {
  .mainHeader--navOpened .mainHeader__toggle::before, .mainHeader--navOpened .mainHeader__toggle::after {
    top: 53px;
  }
}
.mainHeader--navOpened .mainHeader__toggle::before {
  transform: rotate(45deg);
}
.mainHeader--navOpened .mainHeader__toggle::after {
  transform: rotate(-45deg);
}
.mainHeader--navOpened .mainHeader__toggle:hover::before, .mainHeader--navOpened .mainHeader__toggle:hover::after {
  background-color: #D22856;
}
.mainHeader--navOpened .mainHeader__toggle:active::before, .mainHeader--navOpened .mainHeader__toggle:active::after {
  background-color: rgba(210, 40, 86, 0.3);
}
@media (max-width: 1199px) {
  .mainHeader--navOpened .mainHeader__intro {
    background-repeat: no-repeat;
    background-image: url("../img/bg-back-tablet@1x.jpg");
